import React from "react";

// Import styles
import "./style.css";

class TableHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { tableHeaders } = this.props;
    const valid = tableHeaders && tableHeaders.length > 0 ? true : false;
    const renderHeader = valid
      ? tableHeaders.map((item, index) => (
          <div
            key={index}
            style={{ flexGrow: item.grow }}
            className="thead flex-basis-1"
          >
            {item.name}
          </div>
        ))
      : "";
    return <React.Fragment>{renderHeader}</React.Fragment>;
  }
}

export default TableHeader;
