import React from "react";

// Import styles
import "./style.css";

// Import components
import ActionButton from "../ActionButton";

class FormTitle extends React.Component {

  render() {
    const actionButton = this.props.hasAction ? (
      <ActionButton {...this.props} />
    ) : (
      ""
    );
    return (
      <div className="form-title display-flex align-items-center">
        <div className="display-flex title align-items-center">
          <i className={this.props.icon} />
          <span>{this.props.title}</span>
        </div>
        {actionButton}
      </div>
    );
  }
}

export default FormTitle;
