import React from 'react';

// Import components
import Table from "../../components/Table";
import FormTitle from "../../components/FormTitle";
import FormBottom from "../../components/FormBottom";
import {Field, reduxForm} from "redux-form";
import {compose} from "recompose";
import {connect} from "react-redux";
import SelectField from "../../components/SelectField";

// Import render
import {renderInput, renderSelect} from "../../renderfields/light";

// Import validator
import {required} from "../../validator/validator-input";

// Import const
import name from "../../const/name";

// Import css
import "./style.css";

class Orders extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            path: 'orders',
            tableHeaders: [
                {
                    name: "ID",
                    grow: 1,
                    type: "text",
                    editale: true
                }, {
                    name: "Sender name",
                    grow: 2,
                    type: "text",
                    editale: true
                }, {
                    name: "From",
                    grow: 2,
                    type: "text",
                    editale: true
                }, {
                    name: "To",
                    grow: 2,
                    type: "text",
                    editale: true
                }, {
                    name: "Status",
                    grow: 2,
                    type: "text",
                    editale: true
                }, {
                    name: "actions",
                    grow: 1,
                    type: "action"
                }
            ],
            tableBody: []
        };
        this.createOrder = this
            .createOrder
            .bind(this);

        this.submitOrder = this
            .submitOrder
            .bind(this);
    }

    createOrder() {
        this.setState({path: 'createOrder'})
    }

    submitOrder() {}

    render() {
        const {handleSubmit, errMsg} = this.props;
        // debugger
        if (this.state.path === 'orders') {
            return (
                <div>
                    <FormTitle
                        icon="fas fa-plus-circle"
                        title="Orders"
                        actionIcon="far fa-check-circle"
                        hasAction
                        actionName="Create Order"
                        handlerAction={this.createOrder}/>
                    <Table
                        tableName={name.TABLE_ORDERS}
                        tableHeaders={this.state.tableHeaders}
                        tableActions={this.state.tableActions}></Table>
                </div>
            );
        } else {
            return (
                <div >
                    <FormTitle
                        icon="fas fa-plus-circle"
                        title="New Order"
                        actionIcon="far fa-check-circle"
                        hasAction
                        actionName="Save"
                        handlerAction={handleSubmit(this.submitOrder)}/>
                    <form className="light margin-top-24">
                        <div className="summary-error-msg">
                            <span className="message error">{errMsg}</span>
                        </div>
                        <div className='users-section display-flex'>
                            <div className='sender flex-basis-50'>
                                <div className="form-group">
                                    <Field
                                        type="text"
                                        className="form-control"
                                        name="senderName"
                                        required
                                        component={renderInput}
                                        label="Sender Name"
                                        placeholder="Enter sender name"
                                        validate={[required]}/>
                                </div>
                                <div className="form-group">
                                    <Field
                                        type="text"
                                        className="form-control"
                                        name="senderPhone"
                                        required
                                        component={renderInput}
                                        label="Sender Phone"
                                        placeholder="Enter sender phone"
                                        validate={[required]}/>
                                </div>
                            </div>
                            <div className="receiver flex-basis-50">
                                <div className="form-group">
                                    <Field
                                        type="text"
                                        className="form-control"
                                        name="receiverName"
                                        required
                                        component={renderInput}
                                        label="Receiver Name"
                                        placeholder="Enter receiver name"
                                        validate={[required]}/>
                                </div>
                                <div className="form-group">
                                    <Field
                                        type="text"
                                        className="form-control"
                                        name="receiverPhone"
                                        required
                                        component={renderInput}
                                        label="Receiver Phone"
                                        placeholder="Enter receiver phone"
                                        validate={[required]}/>
                                </div>
                                <div className="form-group">
                                    <Field
                                        type="text"
                                        className="form-control"
                                        name="receiverAddress"
                                        required
                                        component={renderInput}
                                        label="Receiver Address"
                                        placeholder="Enter receiver address"
                                        validate={[required]}/>
                                </div>
                            </div>
                        </div>
                        <hr className="line"/>
                        <div className="package-section">
                            <div className="display-flex">
                                <div className="flex-basis-50">
                                    <div className="form-group">
                                        <SelectField
                                            className="form-control"
                                            name="from"
                                            renderSelect={renderSelect}
                                            defaultNull
                                            label="From"/>
                                    </div>
                                </div>
                                <div className="flex-basis-50">
                                    <div className="form-group">
                                        <SelectField
                                            className="form-control"
                                            name="To"
                                            renderSelect={renderSelect}
                                            defaultNull
                                            label="To"/>
                                    </div>
                                </div>
                            </div>
                            <div className="display-flex">
                                <div className="flex-basis-25">
                                    <div className="form-group">
                                        <Field
                                            type="text"
                                            className="form-control"
                                            name="Height"
                                            required
                                            component={renderInput}
                                            label="Height"
                                            placeholder="Enter package height"
                                            validate={[required]}/>
                                    </div>
                                </div>
                                <div className="flex-basis-25">
                                    <div className="form-group">
                                        <Field
                                            type="text"
                                            className="form-control"
                                            name="Depth"
                                            required
                                            component={renderInput}
                                            label="Depth"
                                            placeholder="Enter package depth"
                                            validate={[required]}/>
                                    </div>
                                </div>
                                <div className="flex-basis-25">
                                    <div className="form-group">
                                        <Field
                                            type="text"
                                            className="form-control"
                                            name="Breadth"
                                            required
                                            component={renderInput}
                                            label="Breadth"
                                            placeholder="Enter package breadth"
                                            validate={[required]}/>
                                    </div>
                                </div>
                                <div className="flex-basis-25">
                                    <div className="form-group">
                                        <Field
                                            type="text"
                                            className="form-control"
                                            name="Weight"
                                            required
                                            component={renderInput}
                                            label="Weight"
                                            placeholder="Enter package weight"
                                            validate={[required]}/>
                                    </div>
                                </div>
                            </div>
                            <div className="calculate-section display-flex">
                                <div className="form-group">
                                    <label>Price:
                                        <b>10 $</b>
                                    </label>
                                    <label className="margin-left-24">Time:
                                        <b>2 hours</b>
                                    </label>
                                </div>
                            </div>
                        </div>
                    </form>
                    <FormBottom
                        actionIcon="far fa-check-circle"
                        actionName="Save"
                        handlerAction={handleSubmit(this.submitOrder)}/>
                </div>
            );
        }
    }
}

// const mapStateToProps = state => {     return {}; };

const enhance = compose(reduxForm({form: "CreateOrderForm"}), connect());

export default enhance(Orders);
