import React from "react";

// Import Styles
import "./style.css";

// Import components
import TableHeader from "./components/TableHeader";
import TableBody from "./components/TableBody";

class Table extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { tableHeaders, tableBody, tableActions, tableName } = this.props;
    return (
      <div className="table">
        <div className="table-head display-flex">
          <TableHeader tableHeaders={tableHeaders} />
        </div>
        <div className="table-body">
          <TableBody
            tableName={tableName}
            data={tableBody}
            settingData={{ tableHeaders: tableHeaders, actions: tableActions }}
          />
        </div>
        <div className="table-footer display-flex">{this.props.children}</div>
      </div>
    );
  }
}

export default Table;
