import React from "react";

// Import styles
import "./style.css";
class ActionButton extends React.Component {
  render() {
    return (
      <button
        className="action btn btn-green small"
        onClick={() => this.props.handlerAction()}
      >
        <i className={this.props.actionIcon} />
        <span>{this.props.actionName}</span>
      </button>
    );
  }
}
export default ActionButton;
