import React from "react";
import {Field} from "redux-form";

class SelectField extends React.Component {
  constructor(props) {
    super(props);
    const {options} = this.props;
    this.state = {
      options: options
        ? options
        : [],
      total: 0
    };
  }

  componentDidMount() {
    const {loadData, convertData} = this.props;
    if (loadData) {
      this
        .props
        .loadData()
        .then(data => {
          const options = convertData(data.rows);
          this.setState({options: options, total: data.count});
        })
        .catch(err => {
          console.log(err);
        });
    }
  }

  render() {
    const {defaultNull, name} = this.props;
    const {total} = this.state;
    let size = total > 1
      ? total + 1
      : 2;
    size = size > 10
      ? 10
      : size;
    if (this.state.count === 0) {
      return "";
    }
    return (<Field
      className="form-control"
      name={name}
      component={this.props.renderSelect}
      defaultNull={defaultNull}
      options={this.state.options}
      size={size}
      label={this.props.label}
      placeholder={this.props.placeholder}/>);
  }
}

export default SelectField;
