import React from "react";

export const renderInput = ({
  input,
  className,
  label,
  placeholder,
  type,
  required,
  meta: {
    touched,
    error,
    warning
  }
}) => {
  const inputLabel = required
    ? (
      <span className="label">
        {label}
        <span className="required">*</span>{" "}
      </span>
    )
    : (
      <span className="label">{label}</span>
    );
  return (
    <React.Fragment>
      {inputLabel}
      <input
        className={className}
        {...input}
        placeholder={placeholder}
        required={required}
        type={type}/> {touched && ((error && <span className="message error">{error}</span>) || (warning && <span className="message warning">{warning}</span>))}
    </React.Fragment>
  );
};

