export const renderSelect = ({
  input,
  className,
  label,
  placeholder,
  required,
  defaultNull,
  options,
  meta: {
    touched,
    error,
    warning
  }
}) => {
  const inputLabel = required
    ? (
      <span className="label">
        {label}
        <span className="required">*</span>{" "}
      </span>
    )
    : (
      <span className="label">{label}</span>
    );
  let selectOptions = [];
  if (defaultNull) {
    selectOptions.push(
      <option key={0} value="">
        {"---" + placeholder + "---"}
      </option>
    );
  }
  selectOptions = selectOptions.concat(options);
  return (
    <React.Fragment>
      {inputLabel}
      <select
        {...input}
        value={input.value}
        className={className}
        placeholder={placeholder}
        required={required}>
        {selectOptions}
      </select>
      {touched && ((error && <span className="message error">{error}</span>) || (warning && <span className="message warning">{warning}</span>))}
    </React.Fragment>
  );
};

export const renderMenuSelect = ({
  input,
  className,
  label,
  placeholder,
  required,
  defaultNull,
  options,
  size,
  meta: {
    touched,
    error,
    warning
  }
}) => {
  const inputLabel = required
    ? (
      <span className="label">
        {label}
        <span className="required">*</span>{" "}
      </span>
    )
    : (
      <span className="label">{label}</span>
    );
  let selectOptions = [];
  if (defaultNull) {
    selectOptions.push(
      <option key={0} value="">
        {"---" + placeholder + "---"}
      </option>
    );
  }
  let listOptions = options && options.length > 0
    ? recursiveOptions(options)
    : [];
  selectOptions = selectOptions.concat(listOptions);
  return (
    <React.Fragment>
      {inputLabel}
      <select
        {...input}
        value={input.value}
        className={className}
        required={required}
        placeholder={placeholder}
        size={size}>
        {selectOptions}
      </select>
      {touched && ((error && <span className="message error">{error}</span>) || (warning && <span className="message warning">{warning}</span>))}
    </React.Fragment>
  );
};

const recursiveOptions = options => {
  let listOptions = [];
  if (options && options.length > 0) {
    for (let i = 0; i < options.length; i++) {
      const value = JSON.stringify({level: options[i].level, value: options[i].value});
      listOptions.push(
        <option key={value} value={value}>
          {getLevel(options[i].level) + options[i].text}
        </option>
      );
      if (options[i].subCategories && options[i].subCategories.length > 0) {
        listOptions.push(recursiveOptions(options[i].subCategories));
      }
    }
  }
  return listOptions;
};

const getLevel = level => {
  let levelText = "";
  if (level >= 2) {
    levelText += "|";
    for (let i = 0; i < level - 1; i++) {
      levelText += "---";
    }
  }
  return levelText;
};
