import React from "react";
import { ToastContainer, ToastStore } from "react-toasts";
import { confirmAlert } from "react-confirm-alert";
import { connect } from "react-redux";

// Import styles
import "./style.css";
import "react-confirm-alert/src/react-confirm-alert.css";

// Import const
import tableActions from "../../../../const/tableActions";

class BodyItem extends React.Component {
  constructor(props) {
    super(props);
    const { dataItem, tableName } = this.props;
    this.state = {
      isEditing: false,
      data: dataItem,
      action: {},
      tableName: tableName,
      v: 1
    };
    this.handlerAction = this.handlerAction.bind(this);
  }

  handlerAction(name) {
    const {
      settingData: { actions }
    } = this.props;
    switch (name) {
      case tableActions.EDIT:
        this.setState({
          isEditing: true,
          v: this.state.v + 1
        });
        break;
      case tableActions.UPDATE:
        // eslint-disable-next-line prettier/prettier
        const updateAction = actions.find(m => m.name === tableActions.EDIT).action;
        updateAction(this.state.data)
          .then(res => {
            this.setState({
              isEditing: false,
              v: this.state.v + 1
            });
            ToastStore.success("Updated successfully!", 3000);
          })
          .catch(err => {
            ToastStore.error("Update failed!", 3000);
          });
        break;
      case tableActions.ADD:
        break;
      case tableActions.DELETE:
        let _this = this;
        // eslint-disable-next-line prettier/prettier
        const deleteAction = actions.find(m => m.name === tableActions.DELETE).action;
        confirmAlert({
          title: "Confirm to delete",
          message: "Are you sure to delete this menu item?",
          buttons: [
            {
              label: "Yes",
              // eslint-disable-next-line prettier/prettier
              onClick: () => {
                debugger;
                // eslint-disable-next-line prettier/prettier
                deleteAction(_this.state.data.id, _this.state.tableName)(_this.props.dispatch);
              }
            },
            {
              label: "No"
            }
          ]
        });
        break;
      case tableActions.VIEW:
        break;
      default:
        break;
    }
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.dataItem.id !== nextProps.dataItem.id) {
      this.setState({
        data: nextProps.dataItem,
        v: this.state.v + 1
      });
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    if (this.state.v !== nextState.v) {
      return true;
    }
    return false;
  }

  render() {
    const {
      settingData: { tableHeaders, actions }
    } = this.props;
    const dataColumns = tableHeaders
      ? tableHeaders.map((item, i) => {
          const grow = item.grow;
          if (item.type === "action") {
            const validActions = actions && actions.length > 0 ? true : false;
            let actionButtons = "";
            if (!this.state.isEditing) {
              actionButtons = validActions
                ? actions.map((action, index) => (
                    <button
                      key={index}
                      className="action-button"
                      style={{
                        color: action.color
                      }}
                      onClick={() => this.handlerAction(action.name)}
                    >
                      <i className={action.icon} />
                    </button>
                  ))
                : null;
            } else {
              actionButtons = (
                <button
                  className="action-button"
                  style={{
                    color: "#26C281"
                  }}
                  onClick={() => this.handlerAction(tableActions.UPDATE)}
                >
                  <i className="fas fa-sync-alt" />
                </button>
              );
            }
            return (
              <div
                className="tcol flex-basis-1"
                style={{
                  flexGrow: grow
                }}
                key={i}
              >
                {actionButtons}
              </div>
            );
          } else {
            const itemValue = this.state.isEditing ? (
              <input
                type="text"
                className="line-edit"
                onChange={event => {
                  var newData = this.state.data;
                  newData[item.name] = event.target.value;
                  this.setState({ data: newData, v: this.state.v + 1 });
                }}
                value={this.state.data[item.name]}
              />
            ) : (
              <span>{this.state.data[item.name]}</span>
            );
            return (
              <div
                className="tcol flex-basis-1"
                style={{
                  flexGrow: grow
                }}
                key={i}
              >
                {itemValue}
              </div>
            );
          }
        })
      : null;
    return (
      <div className="trow display-flex">
        {dataColumns}
        <ToastContainer
          store={ToastStore}
          position={ToastContainer.POSITION.TOP_RIGHT}
          lightBackground
        />
      </div>
    );
  }
}

export default connect()(BodyItem);
